import styles from './reset-password.module.css'
import { Button, PasswordInput, Input } from '@ya.praktikum/react-developer-burger-ui-components'
import { FC, FormEvent } from "react"
import {Link, Redirect, useLocation} from 'react-router-dom';
import { useDispatch, useSelector } from '../../services/hooks/useSelector&Dispatch';
import { resetPswUser } from '../../services/actions/user';
import { useForm } from '../../services/hooks/useForm';
import { TLocation } from '../../services/types/data';

export const ResetPassword: FC = () => {
    const {values, handleChange} = useForm({
        password: '',
        token: ''
    })
    const dispatch = useDispatch();
    const resetSuccess = useSelector(store => store.user.resetPswSuccess);
    const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        dispatch(resetPswUser(values.password, values.token))
    }
    const location = useLocation<TLocation>();
    if(location.state?.from !== '/burger-react-ya/forgot-password'){
        return(
            <Redirect to={'/burger-react-ya'}/>
        )
    }
    if(resetSuccess){
        return (
            <Redirect to={'/burger-react-ya/login'}/>
        )
    }
    return (
        <div className={`${styles.container}`}>
            <h1 className={`${styles.title} text text_type_main-medium`}> </h1>
            <form className={`${styles.form}`} onSubmit={handleSubmit}>
                <div className={`${styles.input} mt-6`}>
                    <PasswordInput  placeholder="  " onChange={handleChange} value={values.password} name={'password'} />
                </div>
                <div className={`${styles.input} mt-6 mb-6`}>
                    <Input
                        placeholder="   "
                        name="token"
                        type="text"
                        onChange={handleChange}
                        value={values.token}
                        error={false}
                        errorText=""
                        size="default"
                    />
                </div>
                <Button disabled={!(values.token && values.password)} type="primary" htmlType='submit' size="medium"></Button>
            </form>
            <p className={`text text_type_main-default text_color_inactive mt-20`}> ? <Link to='/burger-react-ya/login' className={`${styles.link}`}></Link></p>
        </div>
    )
}