import styles from './nav-profile.module.css';
import { NavLink } from 'react-router-dom';
import { useDispatch } from '../../services/hooks/useSelector&Dispatch';
import { exit } from '../../services/actions/user';
import { deleteCookie } from '../../utils/cookie';
import { FC } from 'react'

const NavProfile: FC = () => {
    const dispatch = useDispatch();
    const onExit = () => {
        dispatch(exit());
        deleteCookie('authToken');
        deleteCookie('refreshToken');
    }
    return (
        <nav className={`${styles.navigation}`}>
            <NavLink exact to='/burger-react-ya/profile' activeClassName={`${styles.activeLink}`} className={`${styles.link} text text_type_main-medium `}></NavLink>
            <NavLink exact to='/burger-react-ya/profile/orders' activeClassName={`${styles.activeLink}`} className={`${styles.link} text text_type_main-medium `}> </NavLink>
            <NavLink exact to={{ pathname: '/burger-react-ya/login', state: { from: '/burger-react-ya/profile' } }} onClick={onExit} activeClassName={`${styles.activeLink}`} className={`${styles.link} text text_type_main-medium `}></NavLink>
            <p className={`${styles.caption} text text_type_main-default mt-20`}>        </p>
        </nav>
    )
}

export default NavProfile